<template>
	<view class="team-detail-container">
		<!-- È°µÈù¢Â§¥ÈÉ® -->
		<view class="header">
			<view class="team-info">
				<view class="team-avatar">
					{{ teamInfo.name ? teamInfo.name.charAt(0) : 'T' }}
				</view>
				<view class="team-meta">
					<text class="team-name">{{ teamInfo.name || 'Âõ¢ÈòüËØ¶ÊÉÖ' }}</text>
					<text class="member-count">üë• {{ teamInfo.member_count || 0 }} ÊàêÂëò</text>
				</view>
			</view>
		</view>

		<!-- Âõ¢ÈòüÊèèËø∞ -->
		<view class="description-section card" v-if="teamInfo.description">
			<view class="section-title">Âõ¢Èòü‰ªãÁªç</view>
			<text class="description-text">{{ teamInfo.description }}</text>
		</view>

		<!-- Âõ¢ÈòüÊàêÂëòÂàóË°® -->
		<view class="members-section card">
			<view class="section-header">
				<text class="section-title">Âõ¢ÈòüÊàêÂëò</text>
				<text class="member-total">{{ members.length }}‰∫∫</text>
			</view>

			<!-- Âä†ËΩΩÁä∂ÊÄÅ -->
			<view v-if="loading" class="loading-state">
				<text>Âä†ËΩΩ‰∏≠...</text>
			</view>

			<!-- ÊàêÂëòÂàóË°® -->
			<view v-else-if="members.length > 0" class="member-list">
				<view 
					class="member-item" 
					v-for="member in members" 
					:key="member.id"
				>
					<view class="member-avatar">
						<image 
							v-if="member.avatar && member.avatar !== '/images/default-avatar.png'" 
							:src="member.avatar" 
							mode="aspectFill"
							class="avatar-image"
						/>
						<text v-else class="avatar-text">
							{{ member.nickname ? member.nickname.charAt(0) : '?' }}
						</text>
					</view>
					
					<view class="member-info">
						<view class="member-name-row">
							<text class="member-name">{{ member.nickname }}</text>
							<view class="role-badge" :class="member.is_leader ? 'leader' : 'member'">
								<text class="role-icon" v-if="member.is_leader">üëë</text>
								<text class="role-text">{{ member.role_name }}</text>
							</view>
						</view>
						<text class="join-time">{{ formatJoinTime(member.joined_at) }}</text>
					</view>
				</view>
			</view>

			<!-- Á©∫Áä∂ÊÄÅ -->
			<view v-else class="empty-state">
				<text>ÊöÇÊó†ÊàêÂëò‰ø°ÊÅØ</text>
			</view>
		</view>

		<!-- Âõ¢ÈòüÊìç‰Ωú -->
		<view class="actions-section card" v-if="isLeader">
			<button class="action-btn primary" @tap="viewApplications">
				<text class="btn-icon">üìã</text>
				<text class="btn-text">Áî≥ËØ∑ÁÆ°ÁêÜ</text>
			</button>
		</view>
	</view>
</template>

<script>
import { groupApi } from '../../api/index.js'
import { showError, showSuccess, formatDate } from '../../utils/index.js'

export default {
	data() {
		return {
			teamId: null,
			teamInfo: {},
			members: [],
			loading: false,
			isLeader: false
		}
	},
	
	onLoad(options) {
		if (options.id) {
			this.teamId = parseInt(options.id)
			this.loadTeamDetail()
			this.loadTeamMembers()
		}
	},
	
	methods: {
		// Âä†ËΩΩÂõ¢ÈòüËØ¶ÊÉÖ
		async loadTeamDetail() {
			try {
				const response = await groupApi.getTeamDetail(this.teamId)
				if (response.success) {
					this.teamInfo = response.data
					// ËÆæÁΩÆÈ°µÈù¢Ê†áÈ¢ò
					uni.setNavigationBarTitle({
						title: this.teamInfo.name || 'Âõ¢ÈòüËØ¶ÊÉÖ'
					})
				}
			} catch (error) {
				console.error('Âä†ËΩΩÂõ¢ÈòüËØ¶ÊÉÖÂ§±Ë¥•:', error)
				showError('Âä†ËΩΩÂõ¢ÈòüËØ¶ÊÉÖÂ§±Ë¥•')
			}
		},

		// Âä†ËΩΩÂõ¢ÈòüÊàêÂëòÂàóË°®
		async loadTeamMembers() {
			this.loading = true
			try {
				const response = await groupApi.getTeamMembers(this.teamId)
				if (response.success) {
					this.members = response.data.members || []
					this.teamInfo.member_count = response.data.total_members || this.members.length
					
					// Ê£ÄÊü•ÂΩìÂâçÁî®Êà∑ÊòØÂê¶ÊòØË¥üË¥£‰∫∫
					const currentUser = uni.getStorageSync('userInfo')
					if (currentUser) {
						this.isLeader = this.members.some(member => 
							member.id === currentUser.id && member.is_leader
						)
					}
				}
			} catch (error) {
				console.error('Âä†ËΩΩÂõ¢ÈòüÊàêÂëòÂ§±Ë¥•:', error)
				showError('Âä†ËΩΩÊàêÂëòÂàóË°®Â§±Ë¥•')
			} finally {
				this.loading = false
			}
		},

		// Ê†ºÂºèÂåñÂä†ÂÖ•Êó∂Èó¥
		formatJoinTime(dateStr) {
			if (!dateStr) return 'Êú™Áü•'
			try {
				const date = new Date(dateStr)
				const now = new Date()
				const diffTime = now - date
				const diffDays = Math.floor(diffTime / (1000 * 60 * 60 * 24))
				
				if (diffDays === 0) {
					return '‰ªäÂ§©Âä†ÂÖ•'
				} else if (diffDays === 1) {
					return 'Êò®Â§©Âä†ÂÖ•'
				} else if (diffDays < 30) {
					return `${diffDays}Â§©ÂâçÂä†ÂÖ•`
				} else {
					return formatDate(dateStr, 'YYYYÂπ¥MMÊúàDDÊó•')
				}
			} catch (error) {
				return 'Êú™Áü•'
			}
		},

		// Êü•ÁúãÁî≥ËØ∑ÁÆ°ÁêÜ
		viewApplications() {
			uni.navigateTo({
				url: `/pages/team-applications/team-applications?teamId=${this.teamId}&teamName=${encodeURIComponent(this.teamInfo.name)}`
			})
		}
	}
}
</script>

<style lang="scss" scoped>
.team-detail-container {
	min-height: 100vh;
	background-color: #f5f5f5;
	padding: 20rpx;
}

.header {
	background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
	border-radius: 16rpx;
	padding: 40rpx;
	margin-bottom: 20rpx;
	color: white;
}

.team-info {
	display: flex;
	align-items: center;
}

.team-avatar {
	width: 120rpx;
	height: 120rpx;
	border-radius: 60rpx;
	background-color: rgba(255, 255, 255, 0.2);
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 48rpx;
	font-weight: bold;
	margin-right: 30rpx;
}

.team-meta {
	flex: 1;
}

.team-name {
	display: block;
	font-size: 36rpx;
	font-weight: bold;
	margin-bottom: 10rpx;
	line-height: 1.3;
	word-wrap: break-word;
	word-break: break-all;
}

.member-count {
	font-size: 28rpx;
	opacity: 0.9;
}

.card {
	background-color: white;
	border-radius: 16rpx;
	padding: 30rpx;
	margin-bottom: 20rpx;
	box-shadow: 0 2rpx 12rpx rgba(0, 0, 0, 0.1);
}

.section-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 30rpx;
}

.section-title {
	font-size: 32rpx;
	font-weight: bold;
	color: #333;
}

.member-total {
	font-size: 28rpx;
	color: #666;
}

.description-text {
	font-size: 28rpx;
	color: #666;
	line-height: 1.6;
	word-wrap: break-word;
	word-break: break-all;
}

.member-list {
	.member-item {
		display: flex;
		align-items: center;
		padding: 20rpx 0;
		border-bottom: 1rpx solid #f0f0f0;
		
		&:last-child {
			border-bottom: none;
		}
	}
}

.member-avatar {
	width: 80rpx;
	height: 80rpx;
	border-radius: 50%;
	margin-right: 24rpx;
	position: relative;
	overflow: hidden;
	background-color: #f0f0f0;
	display: flex;
	align-items: center;
	justify-content: center;
}

.avatar-image {
	width: 100%;
	height: 100%;
}

.avatar-text {
	font-size: 32rpx;
	color: #666;
	font-weight: bold;
}

.member-info {
	flex: 1;
}

.member-name-row {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-bottom: 8rpx;
}

.member-name {
	font-size: 30rpx;
	color: #333;
	font-weight: 500;
}

.role-badge {
	display: flex;
	align-items: center;
	padding: 4rpx 12rpx;
	border-radius: 12rpx;
	font-size: 24rpx;
	
	&.leader {
		background-color: #fff3cd;
		color: #856404;
	}
	
	&.member {
		background-color: #e7f3ff;
		color: #0066cc;
	}
}

.role-icon {
	margin-right: 4rpx;
}

.join-time {
	font-size: 24rpx;
	color: #999;
}

.loading-state, .empty-state {
	text-align: center;
	padding: 60rpx 0;
	color: #999;
	font-size: 28rpx;
}

.actions-section {
	.action-btn {
		width: 100%;
		height: 88rpx;
		border-radius: 44rpx;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 32rpx;
		border: none;
		
		&.primary {
			background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
			color: white;
		}
	}
	
	.btn-icon {
		margin-right: 12rpx;
	}
}
</style>
